.table-container{width:100%;overflow-x:auto}.table-container table{width:100%;border-collapse:collapse}.table-container table thead{background-color:#f0f0f0}.table-container table thead th{position:sticky;top:0;z-index:1;background-color:#f0f0f0}.table-container table tbody tr td{cursor:pointer}.table-container table th,.table-container table td{padding:12px;text-align:center;border-bottom:1px solid #ccc}.table-container table th a,.table-container table td a{text-decoration:none;color:#0b4085}.table-container table th a:hover,.table-container table td a:hover{text-decoration:underline}.table-container table tr:hover{background-color:#f9f9f9}.table-container .filter-input{margin-bottom:10px;padding:8px;width:100%;max-width:300px;border:1px solid #ccc;border-radius:6px}